import { Component ,OnInit} from '@angular/core';
import { GreetingData } from './model/greeting';
import { HelloWorldService } from './service/HelloWorldService';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{

  greeting: GreetingData;
  error: string;

  constructor(private helloWorldService: HelloWorldService ) {
    
   }

   ngOnInit() {
    this.helloWorldService.getGreetingData("").subscribe(
      data => {
        this.greeting.id = data.id;
        this.greeting.message = data.message;
      },
      error => {
        this.error = error;
      }
    );
  }

  showMessage(id:string){
    this.helloWorldService.getGreetingData(id).subscribe(
      data=>{
        this.greeting.id=data.id;
        this.greeting.message = data.message;
      },
      error => {
        this.error = error;
      }
    )

  }
}
